import React, {Component, lazy, Suspense} from 'react';
import {
	Badge,
	Card,
	CardBody,
	CardFooter,
	CardHeader,
	CardTitle,
	List,
	ListGroup,
	ListGroupItem,
	ListGroupItemHeading,
	ListGroupItemText,
	Modal,
	ModalHeader,
	ModalBody,
	ModalFooter,
	Input,
	Label,
	FormGroup,
	Col,
	Row,
	Button
} from 'reactstrap';

import axios from 'axios';

import {CustomTooltips} from '@coreui/coreui-plugin-chartjs-custom-tooltips';
import {getStyle, hexToRgba} from '@coreui/coreui/dist/js/coreui-utilities'
import Widget02 from '../../views/Widgets/Widget02'

const Loading = () => <div>Loading...</div>

class RestaurantManage extends Component {
	constructor(props) {
		super(props);

		this.state = {
			userID: sessionStorage.getItem("user_id"),
			restaurants: [
				{
					rid: 1,
					cid: 1,
					cname: "pizza",
					name: "Metro Pizza",
					contact: "(702) 486-4368",
					location: "5757 Wayne Newton Blvd",
					star: 2.5,
					photo_url: "https:\/\/s3-media1.fl.yelpcdn.com\/bphoto\/aAoUthFqsND23zdy1sZftw\/o.jpg",
					longitude: -115.1391027,
					latitude: 36.0808997
				},
				{
					rid: 2,
					cid: 6,
					cname: "Caffe",
					name: "Buddy V's Ristorante E Caffe",
					contact: "(702) 424-5786",
					location: "3930 Las Vegas Blvd S",
					star: 3.5,
					photo_url: "https:\/\/s3-media4.fl.yelpcdn.com\/bphoto\/tpzXs9AmhxeZj7CcI2ZFRA\/o.jpg",
					longitude: -115.138026,
					latitude: 36.089494
				}
			],
			success: false,
			info: false,
			categories: []
		};

		this.viewMore = this.viewMore.bind(this);
		this.deleteRestaurant = this.deleteRestaurant.bind(this);
		this.createRestaurant = this.createRestaurant.bind(this);
		this.toggleSuccess = this.toggleSuccess.bind(this);
		this.toggleInfo = this.toggleInfo.bind(this);
		this.updateRestaurants = this.updateRestaurants.bind(this);

		this.updateRestaurants();

		axios.get('http://34.83.23.47:8080/api/category')
			.then(response => {
				this.setState({
					categories: response.data
				});
			}, error => {
				console.log(error);
			});
	}

	toggleSuccess() {
		this.setState({
			success: !this.state.success,
		});
	}

	toggleInfo() {
		this.setState({
			info: !this.state.info,
		});
	}

	updateRestaurants() {
		axios.get('http://34.83.23.47:8080/api/restaurant')
			.then(response => {
				this.setState({
					restaurants: response.data
				});

			}, error => {
				console.log(error);
			});
	}

	viewMore = index => {
		//redirecting to restaurant details
		this.props.history.push({
			pathname: './singleRestaurantManage',
			state: {restaurantID: this.state.restaurants[index].rid}
		});
		sessionStorage.setItem("restaurantID", this.state.restaurants[index].rid);
	}

	deleteRestaurant = rid => {
		this.toggleSuccess();
		//http request, delete a restaurant
		axios.delete('http://34.83.23.47:8080/api/restaurant/' + rid)
			.then(response => {
					this.toggleSuccess();
					this.updateRestaurants();
				},
				error => {
					console.log(error);
				});

	}

	createRestaurant() {
		var r_name = document.getElementById("name").value;
		var r_contact = document.getElementById("contact").value;
		var r_location = document.getElementById("location").value;
		var r_photo = document.getElementById("photo_url").value;
		var r_longitude = document.getElementById("longitude").value;
		var r_latitude = document.getElementById("latitude").value;
		var r_category = document.querySelector('#categories option:checked').value;

		axios.post('http://34.83.23.47:8080/api/restaurant', {
			cid: r_category,
			name: r_name,
			contact: r_contact,
			location: r_location,
			photo_url: r_photo,
			longitude: r_longitude,
			latitude: r_latitude
		})
			.then(response => {
					this.toggleInfo();
					this.toggleSuccess();
					this.updateRestaurants();
				},
				error => {
					console.log(error);
				});

	}

	render() {
		return (
			<div className="animated fadeIn normal-wrapper">
				<div className="wishlist-wrapper">
					<h1>Restaurant Management</h1>
					<Button className="bg-orange text-white" onClick={this.toggleInfo}>
						<b>+ Create Restaurant</b>
					</Button>
				</div>
				<Row style={{marginTop: "1em"}}>
					{this.state.restaurants.map((object, i) => {
						return <Col lg="4" key={i}><Card>
							<CardBody>
								<h4>{object.name}</h4>
								<div><strong>Category: </strong>{object.cname}</div>
								<Button
									className="d-inline-block btn-small btn-round float-right btn-outline-danger"
									onClick={this.deleteRestaurant.bind(this, object.rid)}>Delete</Button>
								<Button className="float-right bg-primary text-white"
										style={{marginRight: "1em"}}
										onClick={this.viewMore.bind(this, i)}>View More</Button>{' '}
							</CardBody>
						</Card>
						</Col>
					})}
				</Row>
				<Modal isOpen={this.state.success} toggle={this.toggleSuccess}
					   className={'modal-success ' + this.props.className}>
					<ModalHeader toggle={this.toggleSuccess}>Operation Success</ModalHeader>
					<ModalBody>
						Your change has been successfully updated.
					</ModalBody>
					<ModalFooter>
						<Button color="success" onClick={this.toggleSuccess}>OK</Button>{' '}
					</ModalFooter>
				</Modal>
				<Modal isOpen={this.state.info} toggle={this.toggleInfo}
					   className={'modal-danger ' + this.props.className}>
					<ModalHeader toggle={this.toggleInfo}>New Restaurant</ModalHeader>
					<ModalBody>
						<FormGroup>
							<Label htmlFor="name">Restaurant Name</Label>
							<Input type="text" id="name" placeholder="Enter restaurant name" required/><br></br>
							<Label htmlFor="contact">Recipe Contact</Label>
							<Input type="text" id="contact" placeholder="Enter contact" required/><br></br>
							<Label htmlFor="location">Recipe Location</Label>
							<Input type="text" id="location" placeholder="Enter location" required/><br></br>
							<Label htmlFor="photo_url">Restaurant Phote (please use url)</Label>
							<Input type="text" id="photo_url" placeholder="Enter photo_url" required/><br></br>
							<Label htmlFor="longitude">Restaurant longitude</Label>
							<Input type="number" id="longitude"
								   placeholder="Ex: +12.0/-12.0 is 12.0 East/West degree longitue"
								   required/><br></br>
							<Label htmlFor="latitude">Restaurant latitude</Label>
							<Input type="number" id="latitude"
								   placeholder="Ex: +12.0/-12.0 is 12.0 North/South degree latitude"
								   required/><br></br>
							<Label htmlFor="categories">Restaurant Category (Single Select)</Label>
							<Input type="select" id="categories" className="400px">
								{
									this.state.categories.map((object, i) => {
										return <option key={i} value={object.cid}>{object.name}</option>
									})
								}
							</Input>
						</FormGroup>
					</ModalBody>
					<ModalFooter>
						<Button color="danger" onClick={this.createRestaurant}>Confirm</Button>{' '}
						<Button color="secondary" onClick={this.toggleInfo}>Cancel</Button>
					</ModalFooter>
				</Modal>
			</div>
		);
	}
}

export default RestaurantManage;
